<template>
  <div class="container">
   <SearchBar @termChange="onTermChange"></SearchBar>
   <VideoList v-bind:videosComponent="videos"></VideoList>\
   <!-- {{ videos.length }} -->
  </div>
</template>

<script>
import axios from 'axios';
import SearchBar from './components/SearchBar';
import VideoList from './components/VideoList';
const API_KEY = 'AIzaSyB3Qt2tdH-XucwyVenVk3sJJWKWg8AwIk0'

export default{
  name:'App',
  components: {
  SearchBar,
  VideoList
},
data:function(){
  return {
    videos:[]
  }
},
methods:{
  onTermChange:function(searchTerm){
    //console.log(searchTerm);
    axios.get('https://www.googleapis.com/youtube/v3/search',{
      params:{
        key:API_KEY,
        type:'video',
        part:'snippet', //what type of info we need for video small part
        q:'searchTerm'
      }
    }).then(response => {
     this.videos = response.data.items
       //console.log(response)
    });
  }
}
};
</script>
